import { instance, LoginMeResponseType, MeResponseType, ResultCodesEnum, ResponseType, ResultCodesForCaptcha } from './api';


type MeResponseDataType = {
    id: number
    email: string
    login: string
 }
 
 type LoginMeResponseDataType = {
    userId: number 
 }
 

export const authAPI = {
    me () {
    return instance.get<ResponseType<MeResponseDataType>>(`auth/me`).then(res => res.data)
    },
 
    login (email: string, password: string, rememberMe = false, captcha: null | string = null) {
       return instance.post<ResponseType<LoginMeResponseDataType, ResultCodesEnum | ResultCodesForCaptcha>>(`auth/login`, {email, password, rememberMe, captcha}).then(res => res.data);
       },
       logout () {
          return instance.delete(`auth/login`)
          }
  }